<template>
  <div class="Activity">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="ruleForm"
      >
       <el-form-item label="">
          <el-radio-group v-model="ruleForm.activity">
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="ruleForm.content">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="pool" required>
          <el-input v-model="ruleForm.pool" autocomplete="off" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="submitForm('ruleForm')"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Activity",
  components: {},
  created() {

  },
  watch: {

  },
  data() {
    var validatePool = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    

    return {
      id: this.$route.query.id,

      ruleForm: {
        activity: "",
        content: "",
        pool: "0",
      },
      rules: {
        pool: [{ validator: validatePool, trigger: "blur" }],
      },
      formLabelWidth: "80px",
    };
  },

  methods: {
    /**
     * 
     */
     submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.request();
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    request() {
      const params = {
        orderId: this.id,
        boostingTask: "=" + this.ruleForm.activity +
        '|=' + this.ruleForm.content +
        '|=' + this.ruleForm.pool+ '|' + "",
        progress:"jsons",
        businessType: "BoostingEvents"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("");

          } else {
            this.$message.error(body.message);
          }
        }
        
      );
    },
    
  }
};
</script>
  
