<template>
  <div class="Skills">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <div class="content-inner">
        <!--  -->
        <div class="list">
          <el-table stripe border size="mini" :data="tableData" style="width: 100%" :max-height="tableMaxHeight">
            <el-table-column v-for="(item, i) in tableHeader" :prop="item.key" :label="item.name" :key="i">
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.fullBreakClass"></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.fullBreakSkill"></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.grailCheck">:</el-checkbox>
                <el-input size="mini" v-model="scope.row.grailNum" autocomplete="off">100</el-input>

              </template>
            </el-table-column>
            <el-table-column fixed="right" label="" width="80">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleAdd(scope.row)">
                </el-button>

              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <div class="content-footer">
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Skills",
  components: {},
  created() {
    if (window.innerHeight < 900) {
      this.tableMaxHeight = "500px";
    } else if (window.innerHeight >= 900) {
      this.tableMaxHeight = "700px";
    }
  },
  watch: {},
  data() {
    return {
      id: this.$route.query.id,

      tableMaxHeight: "",
      tableHeader: [
        {
          key: "career",
          name: "",
        },
        {
          key: "name",
          name: "",
        },
        {
          key: "star",
          name: "",
        },
        {
          key: "come",
          name: "",
        },
        {
          key: "class",
          name: "",
        },
        {
          key: "skill1",
          name: "1",
        },
        {
          key: "skill2",
          name: "2",
        },
        {
          key: "skill3",
          name: "3",
        }
      ],

      tableData: [
        {
          career: "",
          name: "zbb",
          star: "",
          come: "",
          class: "NATURAL",
          skill1: "N",
          skill2: " ",
          skill3: "",
          fullBreakClass: "",
          fullBreakSkill: "RUNNING",
          grailCheck: "",
          grailNum: "",
        },

      ],
    };
  },

  methods: {
    /**
     * 
     */
    handleAdd(row) {

      let data = "=" + row.career +
        "|=" + row.name +
        "|=" + row.star +
        "|=" + row.come +
        "|=" + row.class +
        "|1=" + row.skill1 +
        "|2=" + row.skill2 +
        "|3=" + row.skill3 +
        "|=" + row.fullBreakClass +
        "|=" + row.fullBreakSkill +
        "|=" + row.grailNum;

      const params = {
        orderId: this.id,
        boostingTask: data + '|' + "",
        progress: "jsons",
        businessType: "Follower"
      };
      this.$api.taskBoosting(params).then(
        (res) => {
          const body = res.data;
          if (body.code == "200") {
            this.$message.success("");

          } else {
            this.$message.error(body.message);
          }
        }

      );



    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}

.content {
  padding: 0px 100px 0 20px;

  .ruleForm {
    width: 300px;
  }
}
</style>
  